    color: #282D30;
    height: 18px;
    border-left: 3px solid #8B8B8B;
}
table{
    border-radius: 4px 4px 0px 0px;
    display: block;
    margin: 0 auto;
    width: 100%;
    border-spacing: 0;
    border-collapse: separate;
    margin-bottom: 266px;
    margin-top: 30px;
    height: 100%
}
